body {
  font-family: "Montserrat", sans-serif;
  background-color: #eaede4;
  /* letter-spacing: 3px; */
  color: #665a50;
}
.scoreText {
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: center;
}
.scoreBox {
  display: flex;
  justify-content: center;
}
hr {
  width: 30%;
}
.wrapper {
  display: flex;
  flex-flow: row wrap;
  /* font-weight: bold; */
  text-align: center;
  /* text-transform: uppercase; */
  height: 100vh;
}

.wrapper > * {
  /* padding: 10px; */
  flex: 1 100%;
}

.collaboration {
  padding-left: 2%;
  padding-right: 2%;
}
.soundToggle {
  width: 30%;
  justify-content: flex-start;
  float: left;
  padding-top: 2%;
  padding-bottom: 2%;
  padding-left: 2%;
}
.copyrightContainer {
  width: 70%;
  display: flex;
  justify-content: flex-end;
  background-color: #fcf5ea;
  color: #000;
  padding-top: 2%;
  padding-bottom: 2%;
  padding-right: 2%;
}
.copyrightRight {
  text-transform: uppercase;
}
.copyrightLeft,
.instruction01 {
  font-family: "Sorts Mill Goudy", Sans-serif;
  font-size: 16px;
  font-weight: normal;
  letter-spacing: 1px;
}
.copyrightLeft {
  font-style: oblique;
}

.footer {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  text-align: right;
  padding: 0px;
  background-color: #fcf5ea;
}

.right {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  /* width: 100%; */
  justify-content: center;
  padding: 10px;
  text-align: center;
}

img.windowBox,
img.cafe {
  /* width: 100%;
  height: auto; */
}

.cafe {
  height: 200px;
  width: 520px;
}
.window {
  margin: auto;
  position: relative;
}
.winBox {
  background-size: cover;
  background-position: 50%;
  height: 130px;
  width: 110px;
}

.shake {
  -webkit-animation: shake 0.5s 0.0125s;
  animation: shake 0.5s 0.0125s;
}

@-webkit-keyframes shake {
  0%,
  to {
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
  }

  12.5%,
  37.5%,
  62.5%,
  87.5% {
    -webkit-transform: translate(-10px);
    transform: translate(-10px);
  }

  25%,
  50%,
  75% {
    -webkit-transform: translate(10px);
    transform: translate(10px);
  }
}

@keyframes shake {
  0%,
  to {
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
  }

  12.5%,
  37.5%,
  62.5%,
  87.5% {
    -webkit-transform: translate(-10px);
    transform: translate(-10px);
  }

  25%,
  50%,
  75% {
    -webkit-transform: translate(10px);
    transform: translate(10px);
  }
}

.instruction02,
.subTitle {
  font-family: "Oleo Script", cursive;
}

.instruction02 {
  font-size: 30px;
  padding-top: 5%;
  padding-bottom: 5%;
  color: #c2ac50;
  animation: blinkingText 2.4s infinite;
}

.aside {
  margin: auto;
}

@keyframes blinkingText {
  0% {
    color: transparent;
  }
  49% {
    color: #c7ba85;
  }
  50% {
    color: #ccbc79;
  }
  99% {
    color: #c0ae64;
  }
  100% {
    color: #ccbc79;
  }
}

.subTitle {
  font-size: 20px;
}

.MuiSwitch-track {
  background-color: #b7c5ae !important;
}

.MuiSwitch-switchBase {
  color: #fef6eb !important;
}

.MuiTypography-body1 {
  font-family: "Montserrat", sans-serif !important;
}

@media all and (min-width: 800px) {
  .left {
    order: 1;
  }
  .right {
    order: 2;
  }
  .footer {
    order: 3;
  }
}

@media (min-width: 1100px) {
  .right {
    width: 650px;
  }
  .window {
    width: 650px;
  }
}

@media only screen and (max-width: 1100px) {
  .right {
    justify-content: space-between;
  }
  img.winBox {
    background-size: cover;
    background-position: 50%;
    height: 130px;
    width: 90px;
  }
  .cafe {
    height: 200px;
    width: 410px;
    margin: auto;
  }
  .right {
    width: 400px;
  }
  .window {
    width: 430px;
  }
  footer * {
    padding-right: 0px;
  }
}

@media all and (min-width: 600px) {
  .aside {
    flex: 1 0;
  }
  .right {
    width: 450px;
  }
}

@media only screen and (max-width: 600px) {
  .left {
    width: 100% !important;
  }
  .right {
    justify-content: space-between;
  }
  img.winBox {
    background-size: cover;
    background-position: 50%;
    height: 100px;
    width: 80px;
    padding: 0px;
  }
  .right {
    padding: 0px;
  }
}
